import React from 'react';
import { Platform } from '../types/League';
import NFL_STEP_1 from '../images/tutorials/nfl-cookie/nfl-step-1.png';
import NFL_STEP_2 from '../images/tutorials/nfl-cookie/nfl-step-2.png';
import NFL_STEP_3 from '../images/tutorials/nfl-cookie/nfl-step-3.png';

import ESPN_STEP_1 from '../images/tutorials/espn-cookie/step-1.png';
import ESPN_STEP_2 from '../images/tutorials/espn-cookie/step-2.png';

const tutorialClass = 'flex flex-col gap-2 py-2';

const NFL_TUTORIAL = () => {
    return (
        <div className={tutorialClass}>
            <img
                loading="lazy"
                src={NFL_STEP_1}
                alt="Open your browser's developer tools"
                width="930"
                height="570"
            />
            <img loading="lazy" src={NFL_STEP_2} alt="Go to the console" width="930" height="570" />
            <img
                loading="lazy"
                src={NFL_STEP_3}
                alt="Type copy(document.cookie) and paste the result in the cookie field"
                width="930"
                height="570"
            />
        </div>
    );
};

const ESP_TUTORIAL = () => {
    return (
        <div className={tutorialClass}>
            <p>After being logged in into your ESPN account</p>
            <p>Open your browser's developer tools</p>
            <img
                loading="lazy"
                src={ESPN_STEP_1}
                alt="Open your browser's developer tools"
                width="930"
                height="570"
            />
            <p>
                Paste the following command into the developer console and paste the result in the
                cookie field below.
            </p>
            <img
                loading="lazy"
                src={ESPN_STEP_2}
                alt="Paste the following command into the developer console"
                width="930"
                height="570"
            />
            <blockquote style={{ fontFamily: 'monospace' }}>copy(document.cookie)</blockquote>
        </div>
    );
};

interface Props {
    label: string;
    platform: Platform;
}

export const StepsCollapsible: React.FC<Props> = ({ label, platform }) => {
    return (
        <details>
            <summary className="cursor-pointer w-max">{label}</summary>
            {platform === 'nfl' ? <NFL_TUTORIAL /> : null}
            {platform === 'espn' ? <ESP_TUTORIAL /> : null}
        </details>
    );
};
