import { API_URL, STORAGE_KEYS } from '../constants';
import { League, Platform, platformsOptions } from '../types/League';

export default function LeagueService() {
    async function isLeaguePrivate(platform: Platform, leagueId: string, cookie?: string) {
        const response = await fetch(`${API_URL}/leagueCheck`, {
            method: 'POST',
            body: JSON.stringify({ platform, leagueId, cookie }),
        });

        if (response.status !== 200) {
            const defaultPrivateMessage =
                'Your league seems to be private. Please make it public and try again or send us your cookie.';
            throw new Error(
                platformsOptions[platform].privateLeagueMessage || defaultPrivateMessage
            );
        }
    }

    async function getYahooUserToken(yahooToken: string) {
        if (typeof window !== 'undefined') {
            const request = await fetch(`${API_URL}/index`, {
                method: 'POST',
                body: JSON.stringify({ yahooToken }),
            });

            if (request.status === 200) {
                const yahooApiInfo = await request.json();
                return yahooApiInfo;
            } else {
                throw new Error('Invalid token');
            }
        }
    }

    async function fetchYahooData(): Promise<League | undefined> {
        if (typeof window !== 'undefined') {
            const urlParams = new URLSearchParams(window.location.search);
            const yahoo_token = urlParams.get('code');

            if (!!yahoo_token) {
                try {
                    const savedData = window.localStorage.getItem(STORAGE_KEYS.YAHOO_KEY);
                    if (savedData) {
                        let leagueData: League = JSON.parse(savedData);
                        const yahooApiData = await getYahooUserToken(yahoo_token);
                        leagueData = {
                            ...leagueData,
                            yahoo_api_data: yahooApiData.access_token,
                        };

                        return leagueData;
                    }
                } catch (err) {
                    throw err;
                }
            }
        }
    }

    async function sendLeagueRegisterNotification(league: League) {
        const { name, leagueid, email, platform } = league;
        await fetch(`${API_URL}/handleRegistration`, {
            method: 'POST',
            body: JSON.stringify({
                name,
                leagueid,
                email,
                platform,
            }),
        });
    }

    async function sendLeagueMergedNotification(
        email: string,
        league1: string,
        league2: string,
        mergedLeagueName: string
    ) {
        fetch(`${API_URL}/handleLeagueMerge`, {
            method: 'POST',
            body: JSON.stringify({
                email,
                league1,
                league2,
                mergedLeagueName,
            }),
        });
    }

    return {
        isLeaguePrivate,
        fetchYahooData,
        sendLeagueRegisterNotification,
        sendLeagueMergedNotification,
    };
}
