export const platformTypes = ['sleeper', 'espn', 'yahoo', 'nfl'] as const;
export type Platform = typeof platformTypes[number] | 'merged';

type PlaformOptions = {
    label: string;
    requiredIfPrivate?: boolean;
    beta?: boolean;
    privateLeagueMessage?: string;
};

export const platformsOptions: Record<Platform, PlaformOptions> = {
    sleeper: {
        label: 'Sleeper',
        requiredIfPrivate: false,
    },
    espn: {
        label: 'ESPN',
        requiredIfPrivate: true,
        privateLeagueMessage:
            "Since your league is private we won't be able to get your current data only historic data unless you make your league public or provide us with your espn cookies.",
    },
    yahoo: {
        label: 'Yahoo',
        requiredIfPrivate: true,
    },
    nfl: {
        label: 'NFL',
        requiredIfPrivate: true,
        beta: true,
    },
    merged: {
        label: 'Merged',
    },
};

/**
 * A user in a Fantasy Football platform
 */
export type LeagueUser = {
    name: string;
    /**
     * Assumes that user with a single id are unmerged users and users with multiple ids are merged users
     */
    ids: Array<string>;
};

export type LeagueYear = {
    year: number;
    enabled: boolean;
};

/**
 * A League in a Fantasy Football platform
 */
export type League = {
    /** The id of the league in the database */
    id: string;
    /** The id of the league in the platform */
    leagueid: string;
    /** A firestore timestamp */
    createdAt: {
        seconds: number;
        nanoseconds: number;
    };
    buildWeek: string;
    buildYear: string;
    consolationGames: boolean;
    status?: 'generating' | '';
    platform: Platform;
    /** Tokens used to fetch yahoo leagues data */
    yahoo_api_data?: object;
    /** Cookie used to fetch league data from some platforms that require it when the league is private */
    cookie?: string;
    /** Database id of the User who owns this league */
    userId: string;
    /** For merged leagues this array contains the id of the leagues that were merged  */
    leagues?: Array<string>;
    slug?: string;
    /** Users registered in the league */
    users?: LeagueUser[];
    /** Years this league has enabled */
    years?: LeagueYear[];
    /**
     * League owner name
     * @deprecated Use `userId` to link to the owner instead
     */
    name?: string;
    /**
     * League owner email
     * @deprecated Use `userId` to link to the owner instead
     */
    email?: string;
};

/**
 * Represents the League data entered by the user or obtained during creation process.
 *
 * @remarks Derived from {@link League}
 */

export type LeagueInput = Pick<
    Partial<League>,
    'userId' | 'name' | 'email' | 'slug' | 'leagues' | 'status'
> &
    Pick<League, 'platform' | 'leagueid' | 'consolationGames' | 'cookie' | 'yahoo_api_data'>;
