import React from 'react';
import { Platform } from '../types/League';
import YahooImage from '../images/leagues/yahoo.png';
import ESPNImage from '../images/leagues/espn.png';
import SleeperImage from '../images/leagues/sleeper.png';

export const LeagueFormImage: React.FC<{ platform: Platform }> = ({ platform }) => {
    let src = null;
    switch (platform) {
        case 'yahoo':
            src = YahooImage;
            break;
        case 'espn':
            src = ESPNImage;
            break;
        case 'sleeper':
            src = SleeperImage;
            break;
        default:
            return null;
    }

    if (src === null) return null;

    return (
        <img
            src={src}
            alt={`Copy the numeric part of the url league for ${platform}`}
            className="my-4 w-ful
     object-contain h-14"
            height="55"
            width="630"
        />
    );
};
