import React, { useCallback } from 'react';
import { useBanner } from '../../context/BannerContext';
import Logo from '../../images/logo.jpeg';
import { Platform, platformTypes, platformsOptions, League, LeagueInput } from '../../types/League';
import { StepsCollapsible } from '../StepsCollapsible';
import { useUserStore, UserState } from '../../store/user';
import { STORAGE_KEYS, YAHOO_URL } from '../../constants';
import { Loading } from '../Loading';
import { LeagueFormImage } from '../LeagueFormImage';
import GoogleButton from '../GoogleButton';
import { DiscordButton } from '../DiscordButton';
import { Link } from 'gatsby';
import services from '../../services';
import { convertIntoFlatObject, navigateToExternalUrl } from '../../utils';
import { useDebouncedEffect } from '../../hooks/useDebouncedEffect';
import { APP_ROUTES } from '../../constants';

export interface LeagueFormState extends LeagueInput {
    name?: string;
    email?: string;
    password?: string;
}

export type LeagueFormStateConfig = {
    [K in keyof LeagueFormState]: {
        initialValue: LeagueFormState[K];
        disabled: boolean;
    };
};

interface Props {
    title: string;
    config?: LeagueFormStateConfig;
    onSubmit: (values: LeagueFormState) => Promise<void>;
    standalone?: boolean;
    showDiscordButton?: boolean;
    localAlertText?: string;
    sendNotification?: boolean;
}

const spacingClass = 'flex flex-col gap-3';
export const LeagueForm: React.FC<Props> = ({
    title,
    config = {},
    onSubmit,
    standalone,
    showDiscordButton,
    localAlertText,
    sendNotification,
}) => {
    const initialValues: LeagueFormState = convertIntoFlatObject(config, 'initialValue');
    const [formState, setFormState] = React.useState<LeagueFormState>({
        ...{
            platform: platformTypes[0],
            leagueid: '',
            consolationGames: false,
            cookie: '',
            name: '',
            email: '',
        },
        ...initialValues,
    });
    const [formLoading, setFormLoading] = React.useState(false);
    const [showCookiesField, setShowCookiesField] = React.useState(false);
    const [localAlert, setLocalAlert] = React.useState(localAlertText || '');
    const { showBanner, hideBanner } = useBanner();
    const { user, loading, createGoogleAccount } = useUserStore((state: UserState) => ({
        user: state.user,
        loading: state.loading,
        createGoogleAccount: state.createGoogleAccount,
    }));

    function shouldShowContactInfoSection(): boolean {
        let hasLoginParam = false;
        if (typeof window !== 'undefined') {
            const urlParams = new URLSearchParams(window.location.search);
            hasLoginParam = !!urlParams.get('login');
        }

        return !user || hasLoginParam;
    }
    const showContactInfoSection = shouldShowContactInfoSection();

    function onUnmount() {
        hideBanner();
    }

    React.useEffect(() => {
        setLocalAlert(localAlertText || '');
    }, [localAlertText]);

    React.useEffect(() => {
        async function fetchYahooData() {
            try {
                const yahooData = await services.leagueService.fetchYahooData();
                console.log({ yahooData });
                if (yahooData) {
                    setFormState(yahooData);
                }
            } catch (err) {
                console.log(err);
                //
            }
        }
        if (typeof window !== 'undefined') {
            fetchYahooData();
        }

        return onUnmount;
    }, []);

    // sets user data after the logged user is fetched
    React.useEffect(() => {
        if (user) {
            setFormState((prev) => {
                return {
                    ...prev,
                    name: user.displayName,
                    email: user.email,
                };
            });
        }
    }, [user]);

    console.log({ formState });
    // adds alert for platforms in beta
    React.useEffect(() => {
        if (platformsOptions[formState.platform].beta) {
            showBanner({
                text: `WARNING: ${formState.platform}.com is in beta`,
                type: 'error',
            });
        } else {
            hideBanner();
        }
    }, [formState.platform]);

    const checkForPrivateLeague = useCallback(() => {
        async function checkIfLeagueIsPrivate() {
            try {
                await services.leagueService.isLeaguePrivate(
                    formState.platform,
                    formState.leagueid,
                    formState.cookie
                );
                setShowCookiesField(false);
                setFormState((prev) => ({ ...prev, cookie: '' }));
                setLocalAlert('');
            } catch (error) {
                setShowCookiesField(true);
                setLocalAlert(error.message);
            }
        }

        if (formState.leagueid) {
            checkIfLeagueIsPrivate();
        }
    }, [formState.platform, formState.leagueid, formState.cookie]);

    useDebouncedEffect(
        checkForPrivateLeague,
        [formState.platform, formState.leagueid, formState.cookie],
        500
    );

    function handleInputChange(event: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) {
        const { name } = event.target;
        let value: string | boolean =
            (event.target as HTMLInputElement).type === 'checkbox'
                ? (event.target as HTMLInputElement).checked
                : event.target.value;

        setFormState((prevState) => ({
            ...prevState,
            [name]: value,
        }));
    }

    async function handleFormSubmit(e: React.FormEvent<HTMLFormElement>) {
        e.preventDefault();
        const form = e.currentTarget;
        if (form.reportValidity()) {
            setFormLoading(true);
            if (sendNotification) {
                services.leagueService.sendLeagueRegisterNotification(formState as League);
            }
            if (formState.platform === 'yahoo' && !formState.yahoo_api_data) {
                window.localStorage.setItem(STORAGE_KEYS.YAHOO_KEY, JSON.stringify(formState));
                navigateToExternalUrl(YAHOO_URL);
            } else {
                const sanitizedFormState: LeagueFormState = Object.keys(formState).reduce(
                    (acc, key) => {
                        if ((formState as any)[key] !== undefined) {
                            (acc as any)[key] = (formState as any)[key];
                        }

                        return acc;
                    },
                    {} as LeagueFormState
                );

                await onSubmit({ ...sanitizedFormState, userId: user?.uid });
            }
            setFormLoading(false);
        }
    }

    const isContactInfoRequired =
        formState.email || formState.name || formState.password ? true : false;

    const formValuesHavenotChanged =
        Object.keys(initialValues).length > 0
            ? Object.keys(initialValues).every((key) => {
                  // @TODO: if at some point the user needs to update only their cookie remove it rom the check below
                  if (key === 'name' || key === 'email' || key === 'cookie') return true;
                  return (formState as any)[key] === (initialValues as any)[key];
              })
            : false;

    if (loading) return <Loading />;

    return (
        <div className={`w-full p-6 ${standalone ? '' : 'md:p-10'}`}>
            {!standalone && (
                <div>
                    <img src={Logo} alt="logo" className="object-contain h-24" />
                </div>
            )}

            <form
                className={`${spacingClass} ${standalone ? '' : 'mt-4'}`}
                onSubmit={handleFormSubmit}
            >
                <div aria-live="polite">
                    {localAlert.length > 0 && <p className="text-danger-500">{localAlert}</p>}
                </div>
                <div className={`${spacingClass}`}>
                    <h2 className="h3">{title || 'League Information'}</h2>
                    <div className="form-group">
                        <label htmlFor="platform">Platform</label>
                        <select
                            id="platform"
                            name="platform"
                            className="base-field base-select"
                            required
                            value={formState.platform}
                            onChange={handleInputChange}
                        >
                            {platformTypes.map((platform) => (
                                <option key={platform} value={platform}>
                                    {platformsOptions[platform].label}
                                </option>
                            ))}
                        </select>
                    </div>
                    <div className="form-group">
                        <LeagueFormImage platform={formState.platform} />
                        <label htmlFor="leagueid">League Id</label>
                        <input
                            name="leagueid"
                            id="leagueid"
                            className="base-field"
                            required
                            aria-describedby="leagueid-info"
                            value={formState.leagueid}
                            onChange={handleInputChange}
                        />
                        <span id="leagueid-info" className="base-field__info">
                            See image above, please enter just the numbers
                        </span>
                    </div>
                    {showCookiesField && (
                        <div className="form-group">
                            <StepsCollapsible label="How to get your cookies?" platform="espn" />
                            <label htmlFor="cookie">Your {formState.platform} cookie</label>
                            <input
                                name="cookie"
                                id="cookie"
                                className="base-field"
                                required
                                value={formState.cookie}
                                onChange={handleInputChange}
                            />
                        </div>
                    )}
                    <div className="form-group form-group--horizontal !hidden">
                        <input
                            name="consolationGames"
                            id="consolationGames"
                            className="base-field"
                            type="checkbox"
                            checked={formState.consolationGames}
                            onChange={handleInputChange}
                        />
                        <label htmlFor="consolationGames">
                            Include consolation bracket games in the data? (Beta)
                        </label>
                    </div>
                </div>
                {showContactInfoSection && (
                    <div className={`${spacingClass}`}>
                        <h2 className="h3">Contact Information</h2>
                        <div className="form-group">
                            <label htmlFor="name">Name</label>
                            <input
                                name="name"
                                id="name"
                                className="base-field"
                                required={isContactInfoRequired}
                                value={formState.name}
                                onChange={handleInputChange}
                                disabled={
                                    !!user?.displayName && user.displayName === formState.name
                                }
                            />
                        </div>
                        <div className="form-group">
                            <label htmlFor="email">Email</label>
                            <input
                                name="email"
                                id="email"
                                type="email"
                                className="base-field"
                                required={isContactInfoRequired}
                                value={formState.email}
                                onChange={handleInputChange}
                                disabled={!!user?.email && user.email === formState.email}
                            />
                        </div>

                        {!user && (
                            <div className="form-group">
                                <label htmlFor="password">Password</label>
                                <input
                                    name="password"
                                    id="password"
                                    type="password"
                                    className="base-field"
                                    required={isContactInfoRequired}
                                    value={formState.password}
                                    onChange={handleInputChange}
                                />
                            </div>
                        )}
                    </div>
                )}

                <div className="flex flex-col gap-2 mt-3 md:flex-row">
                    <button
                        className="!px-16 base-button w-max"
                        disabled={formLoading || formValuesHavenotChanged}
                    >
                        {showContactInfoSection ? 'Sign Up' : 'Submit'}
                    </button>
                    {!user && (
                        <GoogleButton
                            title="Sign up"
                            onClick={() =>
                                createGoogleAccount({
                                    redirectUrl: window.location.pathname + '?login=google',
                                })
                            }
                        />
                    )}
                </div>
                <div>
                    Already have an account? <Link to={APP_ROUTES.LOGIN}>Log In</Link>
                </div>
                {showDiscordButton && <DiscordButton />}
            </form>
        </div>
    );
};
