export function navigateToExternalUrl(url: string, newTab = false) {
    const blankLink = document.createElement('a');
    blankLink.setAttribute('href', url);
    if (newTab) {
        blankLink.setAttribute('target', '_blank');
    }
    blankLink.style.display = 'none';
    document.body.appendChild(blankLink);
    blankLink.click();
    document.body.removeChild(blankLink);
}

export async function copyUrlToClipboard(url: string) {
    if (!navigator.clipboard) {
        const el = document.createElement('textarea');
        el.value = url;
        document.body.appendChild(el);
        el.select();
        document.execCommand('copy');
        document.body.removeChild(el);
    } else {
        await navigator.clipboard.writeText(url);
    }
    window.alert('URL copied to clipboard!');
}

export function convertIntoComposedObject<T>(obj: object, key: string): T {
    const result: T = Object.keys(obj).reduce((acc, currentKey) => {
        (acc as any)[currentKey] = {
            [key]: (obj as any)[currentKey],
        };

        return acc;
    }, {} as T);

    return result;
}

export function convertIntoFlatObject<T>(obj: object, key: string): T {
    const result: T = Object.keys(obj).reduce((acc, currentKey) => {
        (acc as any)[currentKey] = (obj as any)[currentKey][key];
        return acc;
    }, {} as T);

    return result;
}

export function getChangedValues<T extends {}>(initialObject: T, newObject: T): Partial<T> {
    const changedValues: T = Object.keys(newObject).reduce((acc, key) => {
        if ((initialObject as any)[key] !== (newObject as any)[key]) {
            (acc as any)[key] = (newObject as any)[key];
        }
        return acc;
    }, {} as T);

    return changedValues;
}
